import React, { Component } from 'react'
import { Card, Button, Typography } from 'material-ui'
import { CardContent, CardActions } from 'material-ui/Card'
import {Link} from 'react-router-dom'

class GenreCard extends Component {
  render() {
    let genreUrl = "/genre?" + this.props.name
    return (
        <Card>
          <CardContent>
            <Typography style={{fontSize: "large", marginBottom: 20}} variant="headline" component="h2">
              {this.props.name}
            </Typography>
            <Typography>
              {this.props.desc}
            </Typography>
          </CardContent>
          <CardActions>
            <Link to={genreUrl}>
              <Button size="small" color="primary">Videos</Button>
            </Link>
          </CardActions> 
        </Card>
    )
  }
}

export default GenreCard



